@import "~admin-stylesheets/colors";
@import "~admin-stylesheets/variables";

.nab-heatmap-results-header {

	align-items: stretch;
	background: $color__background-editor;
	border-bottom: 1px solid $color__border-editor;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	height: $editor-bar-height;
	justify-content: space-between;
	left: 0;
	padding: 8px;
	z-index: 30;

	&__info {
		align-items: center;
		display: flex;
		flex-direction: row;
		min-width: 5em;
		width: 100%;

		h1 {
			line-height: 1.3;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}

	&__right-promo-button {
		justify-self: flex-end;
		margin-left: auto;
	}

	&__experiment-icon {
		height: ($editor-bar-height - 16) * 0.8;
		min-width: ($editor-bar-height - 16) * 0.8;
		width: ($editor-bar-height - 16) * 0.8;
	}

	&__settings {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
	}

	&__stop-button.components-button.is-button {
		height: 33px;
		line-height: 32px;
		font-size: 13px;
	}

	&__stop-button--is-saving {
		animation: pulse 500ms infinite;
		color: $color__text-disabled;
		display: flex;
		padding: 0 1.5em;

		svg {
			margin-right: 4px;
		}
	}

	&__title {
		margin: 0 0.5em;
		font-weight: 400;
	}

}

.nab-alternative__letter {
	border-radius: 50%;
	color: $color__text-alternative;
	display: inline-block;
	flex-grow: 0;
	font-size: 1.3rem;
	height: 1em;
	line-height: 1;
	margin: 0;
	margin-right: 0.5em;
	padding: 0.3em;
	text-align: center;
	transition: 250ms background ease-in-out, 250ms color ease-in-out;
	width: 1em;
}

$colors: (
	$color__background-alternative-a,
	$color__background-alternative-b,
	$color__background-alternative-c,
	$color__background-alternative-d,
	$color__background-alternative-e,
	$color__background-alternative-f
);
$i: 0;

@each $color in $colors {
	.nab-alternative__letter--#{$i} {
		background: $color;
	}
	$i: $i + 1;
}
