@import "~admin-stylesheets/breakpoints";
@import "~admin-stylesheets/colors";

.nab-heatmap {

	&__results-loader {
		margin: 4em auto 0;
	}

	&__iframe {
		border-radius: 5px;
		box-shadow: 5px 5px 10px $color__box-shadow;
		left: 50%;
		margin: 0 auto;
		position: absolute;
		transform-origin: top;
		transform: translate(-50%) scale(0.7);
	}

}
