@import "~admin-stylesheets/breakpoints";
@import "~admin-stylesheets/mixins";
@import "~admin-stylesheets/variables";

#wpcontent {
	padding-left: 0 !important;
}

.nab-heatmap-results-layout {

	.nab-heatmap-results-header {

		@include break-small {
			position: fixed;
			right: 0;
			top: $admin-bar-height-big;
		}

		@include break-medium {
			top: $admin-bar-height;
		}
	}

	&__body {

		@include break-small {
			margin-top: $editor-bar-height;
		}

		@include break-medium {
			margin-right: $editor-sidebar-width;
		}
	}

	&__content {
		display: flex;
		box-sizing: border-box;
		padding: 1em;
		position: relative;
		overflow: hidden;
		width: 100%;
	}

	.results-post-title {
		padding: 0;

		&__block {
			max-width: none;
			padding: 0;
		}

		&__input {
			font-family: $font-family;
			font-size: 2em;
			font-weight: 400;
		}
	}

	.nab-heatmap-results-sidebar {
		margin-bottom: 2em;

		@include break-medium {
			margin-bottom: 0;
			height: 100vh;
			overflow: hidden;
			padding-top: $editor-bar-height;
			position: fixed;
			right: 0;
			top: 0;
			width: $editor-sidebar-width;
		}
	}

}

@include editor-left(".nab-heatmap-results-layout .nab-heatmap-results-header");
